import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../state/actions/index';


class FsiGreeting extends Component {

    componentDidMount = () => {
        const accessToken = localStorage.getItem("accessToken");
        // const accessToken = "7a84e1b9f19b3d68b826b737d9edc918";

        if (accessToken) {
            console.log(accessToken);
            this.props.remoteGetUserProfile(accessToken);
        }

    }

    clickHandeler = () => {
        window.location = 'http://' + window.location.host + '/fsi-credit-card-dispute-customer/en/landing_secure.page';
        //   window.location = 'http://localhost:8181/fsi-credit-card-dispute-customer/en/landing_secure.page';
    }

    render() {
        const userProfile = this.props.userProfile;
        var graph = require('../css/monthyIncomebg.png');

        if (userProfile.name == 'Jon') {
            graph = require('../css/monthyIncomebg.png');

        }
        if (userProfile.name == 'Khal') {
            graph = require('../css/monthyIncomebg2.png');

        }
        if (userProfile.name == 'Arya') {
            graph = require('../css/monthyIncomebg3.png');
        }
        return (
            <div id="gopage" class="blackan" href="" onClick={this.clickHandeler}>
                <div className={"widget lazur-bg monthyIncome"} style={{ backgroundImage: "url(" + graph + ")" }}>
                    <div class="flot-chart">
                        <div class="flot-chart-content" id="flot-chart2">
                            <div className="row">
                                <div className="col-sm-6">
                                    <div className="row">
                                        <div className="col-sm-12" style={{ marginLeft: "10%", paddingTop: "20px" }}>
                                            <h2><b>Diamond Travel Crad
                                            <br /> by Red Hat - 7358</b> </h2>
                                        </div>
                                    </div>
                                    <div style={{ marginLeft: "3%", paddingTop: "60px" }} >
                                        <div className="row">
                                            <div className="col-sm-12">
                                                <h3 >Minimum Payment</h3>
                                                <p style={{ fontSize: "16px", paddingTop: "10px" }}>{userProfile.minCredit}</p>
                                            </div>
                                        </div>
                                        <div className="row" style={{ paddingTop: "15px" }}>
                                            <div className="col-sm-12">
                                                <h3 >Available Credit</h3>
                                                <p style={{ fontSize: "16px", paddingTop: "10px" }}>{userProfile.avaCredit}</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-sm-6">
                                    <div className="row">
                                        <div className="col-sm-12" >
                                            <br />
                                            <div style={{ marginLeft: "65%", marginTop: "40px" }}>
                                                <p><b>{userProfile.cStatus} MEMBER</b></p>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-sm-12" >
                                            <br />
                                            <div style={{ marginLeft: "65%" }}>
                                                <p style={{ fontSize: "40px", paddingTop: "40px" }}>{userProfile.crntBal}</p>
                                                <p style={{ marginLeft: "55px" }}>Current Balance</p>
                                                <button className="btn btn-primary" style={{ marginLeft: "30px", backgroundColor: "#fff", color: "#20BFC1" }}>Make Payment</button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
const mapStateToProps = state => {
    return {
        userProfile: state.transactionDisputeReducer.userProfile,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        remoteGetUserProfile: (accessToken) => dispatch(actions.remoteGetUserProfile(accessToken)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(FsiGreeting);



